<template>
    <section class="pc-container pb-4 px-4">
        <div class="pcoded-content">
            <div class="card">
                <div class="card-header py-3">
                    <div class="d-flex align-items-center">
                        <h5>Lista de Organos Jurisdiccionales</h5>
                        <button class="btn btn-success btn-sm px-4 ml-auto rounded-1" @click="mostrarModal">
                            Nuevo
                        </button>
                    </div>
                </div>
                <div class="card-body">
                    <div class="row">
                        <label class="form-label">Seleccione un Distrito Judicial</label>
                    </div>
                    <div class="row mb-3">
                        <div class="col-md-3">
                            <select class="form-control" @change="leer()" v-model="distrito">
                                <option value="" disabled selected>-- Seleccionar</option>
                                <option v-for="distrito in distritos" :value="distrito.judis_id">
                                    {{ distrito.judis_nombre }}
                                </option>
                            </select>
                        </div>
                        <div class="col-md-3">
                            <button class="btn btn-primary btn-lg w-100" @click="guardarfavorito()"
                                :disabled="favoritos.length == 0">
                                Guardar Concurrentes
                            </button>
                        </div>
                    </div>
                    <div class="table-responsive">
                        <table class="table" id="miTabla1">
                            <thead>
                                <tr>
                                    <th class="text-center">#</th>
                                    <th class="col-lg-7">Nombre</th>
                                    <th class="text-center">Concurrente</th>
                                    <th class="text-center">Opciones</th>
                                </tr>
                            </thead>
                            <tbody>
                                <tr v-if="distrito == ''" class="text-center">
                                    <td colspan="4" class="py-3 text-secondary">Seleccione un Distrito Judicial</td>
                                </tr>
                                <tr v-for="juzgado in juzgados" :key="juzgado.co_id">
                                    <td class="text-center py-2 my-0">
                                        {{ juzgado.co_id }}
                                    </td>
                                    <td>{{ juzgado.co_nombre }}</td>
                                    <td class="text-center">
                                        <input type="checkbox" class="form-check-input" v-model="juzgado.co_isFavorite"
                                            @change="marcarFavorito(juzgado)">
                                    </td>
                                    <td class="text-center">
                                        <a href="javascript:void(0)" @click="editar(juzgado.co_id)"
                                            class="text-primary px-2">
                                            <span><i data-feather="edit"></i></span>
                                        </a>
                                        <a href="javascript:void(0)" @click="borrar(juzgado.co_id)"
                                            class="text-danger px-2">
                                            <span><i data-feather="trash"></i></span>
                                        </a>
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
            <div v-if="showModal" class="modal" @click="cerrarModal">
                <div class="modal-content card border-0" @click.stop>
                    <div class="card-header">
                        <h5 class="text-center">
                            {{ juzgado.co_id ? 'Actualizar Organo Jurisdiccional' : 'Registro de nuevo Organo Jurisdiccional' }}
                        </h5>
                    </div>
                    <div class="card-body mb-0 pb-4">
                        <div class="row mb-3">
                            <div class="col-md-12">
                                <label class="form-label">Distrito Judicial</label>
                                <select class="form-control" v-model="distrito">
                                    <option value="" disabled selected>--SELECCIONAR</option>
                                    <option v-for="distrito in distritos" :value="distrito.judis_id">
                                        {{ distrito.judis_nombre }}
                                    </option>
                                </select>
                            </div>
                        </div>
                        <div class="row">
                            <div class="col-md-12">
                                <div class="form-group mb-0">
                                    <label class="form-label">Nombre</label>
                                    <input class="form-control" v-model="juzgado.co_nombre" @keyup.enter="guardar"
                                        @blur="validarNombre" @input="errores.co_nombre = false"
                                        :class="{ 'has-error': errores.co_nombre }">
                                </div>
                                <span class="error-message" v-if="errores.co_nombre">Ingrese un nombre de un Organo Jurisdiccional.</span>
                            </div>
                        </div>
                    </div>
                    <div class="card-footer border-0 mt-0 pt-0">
                        <div class="d-flex justify-content-end">
                            <button class="btn btn-secondary btn-lg  px-4 mx-2 rounded" @click="cerrarModal">Cerrar</button>
                            <button class="btn btn-primary btn-lg  px-4 rounded" @click="guardar">
                                {{ juzgado.co_id ? 'Actualizar' : 'Guardar' }}
                            </button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>
  
<script>
import feather from 'feather-icons';
import $ from 'jquery';
import 'datatables.net-bs5';
import 'datatables.net-bs5/css/dataTables.bootstrap5.min.css';
import 'datatables.net-buttons-bs5';
import 'datatables.net-buttons-bs5/css/buttons.bootstrap5.min.css';
import { mapActions } from 'vuex';

export default {
    name: "abogado",
    created() {
        this.cargardistritos();
    },

    mounted() {
        this.$nextTick(() => {
            feather.replace();
        });
    },
    data() {
        return {
            dataTable: null,
            showModal: false,
            distrito: "",
            distritos: [],
            juzgados: [
            ],
            juzgado: {
                co_id: "",
                co_nombre: "",
                favorito: false
            },
            errores: {
                co_nombre: false
            },
            favoritos: []

        };
    },

    methods: {
        ...mapActions(["get", "post"]),
        marcarFavorito(juzgado) {
            const existingIndex = this.favoritos.findIndex(item => item === juzgado.co_id);
            console.log(existingIndex);
            if (juzgado.co_isFavorite == 1) {
                if (existingIndex == -1) {
                    this.favoritos.push(juzgado.co_id);
                }
            } else {
                if (existingIndex != -1) {
                    this.favoritos.splice(existingIndex, 1);
                }
            }
            console.log(this.favoritos);
        },

        // Validacion de campos
        validarCampo(campo, propiedadError) {
            if (this.juzgado[campo] == "") {
                this.errores[propiedadError] = true;
            } else {
                this.errores[propiedadError] = false;
            }
        },
        guardarfavorito() {
            this.post({
                url: this.$store.getters.get__url + "/juzgado/favorite",
                token: this.$store.getters.get__token,
                params: {
                    favoritos: this.favoritos,
                }
            })
                .then((response) => {
                    if (response.state == 0) {
                        this.favoritos = [];
                        this.leer();
                    }
                    else {
                    }
                })
                .catch((errors) => {
                });
        },

        cargardistritos() {

            this.get({
                url: this.$store.getters.get__url + "/judicialdistrict",
                token: this.$store.getters.get__token,
                params: {
                }
            })
                .then((response) => {
                    console.log(response);
                    this.distritos = response.data;
                    this.distrito.judis_id = distritos[16].judis_id;
                })
                .catch((errors) => { });
        },

        validarNombre() {
            this.juzgado.co_nombre = this.juzgado.co_nombre;
            this.validarCampo('co_nombre', 'co_nombre');
        },

        validarCampos() {
            this.validarNombre();
            return !Object.values(this.errores).some(error => error);
        },

        async leer() {
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + "/juzgado",
                    token: this.$store.getters.get__token,
                    params: {
                        judis_id: this.distrito
                    }
                });

                console.log(response);
                if (response.state == 0) {
                    this.favoritos = [];
                    //marcar los favitos
                    if (response.data.length > 0) {
                        for (let i = 0; i < response.data.length; i++) {
                            this.marcarFavorito(response.data[i]);
                        }
                    }
                    this.juzgados = response.data;
                    this.initDataTable();
                } else {
                    console.log("error");
                }
            } catch (error) {
                console.error(error);
            }
        },

        async crear() {
            if (!this.validarCampos()) {
                console.error('Error en la validacion de los campos de registro.');
                return;
            }
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + "/juzgado/store",
                    token: this.$store.getters.get__token,
                    params: {
                        judis_id: this.distrito,
                        co_nombre: this.juzgado.co_nombre
                    }
                });
                if (response.state == 0) {
                    this.leer();
                    this.juzgado = {
                        co_id: "",
                        co_nombre: ""
                    },
                        this.showModal = false;

                } else {
                    console.log("error");
                }
            } catch (error) {
                console.error(error);
            }
        },

        async actualizar(id) {
            if (!this.validarCampos()) {
                console.error('Error en la validacion de los campos de registro.');
                return;
            }
            try {
                const response = await this.post({
                    url: this.$store.getters.get__url + "/juzgado/update",
                    token: this.$store.getters.get__token,
                    params: {
                        judis_id: this.distrito,
                        co_id: id,
                        co_nombre: this.juzgado.co_nombre
                    }
                });
                if (response.state === 0) {
                    this.showModal = false;
                    this.juzgado = {
                        co_id: "",
                        co_nombre: ""
                    },
                        await this.leer();
                } else {
                    console.error('Error en la respuesta del servidor', response);
                }
            } catch (error) {

            }
        },

        async borrar(id) {
            try {
                console.log(id);
                const response = await this.post({
                    url: this.$store.getters.get__url + "/juzgado/destroy",
                    token: this.$store.getters.get__token,
                    params: {
                        co_id: id,
                    },
                });

                if (response.state === 0) {
                    console.log(response);
                    this.juzgado = {
                        co_id: "",
                        co_nombre: ""
                    },
                        await this.leer();
                } else {
                    console.error('Error en la respuesta del servidor', response);
                }
            } catch (error) {
                console.error("Error en la solicitud de eliminaciÃ³n", error);
            }
        },

        editar(id) {
            try {
                const juzgadoEdit = this.juzgados.find(juzgado => juzgado.co_id === id);
                if (juzgadoEdit) {
                    this.juzgado = { ...juzgadoEdit };
                    this.showModal = true;
                    console.log(id);
                } else {
                    console.error('Juzgado no encontrado');
                }
            } catch (error) {

            }
        },

        guardar() {
            try {
                if (!this.juzgado.co_id) {
                    this.crear();
                } else {
                    this.actualizar(this.juzgado.co_id);
                }
            } catch (error) {
                console.error(error);
            }
        },

        initDataTable() {
            if (this.dataTable) {
                this.dataTable.destroy();
            }

            this.$nextTick(() => {
                this.dataTable = $('#miTabla1').DataTable({
                    language: {
                        url: '//cdn.datatables.net/plug-ins/1.13.6/i18n/es-ES.json',
                    },
                });
                feather.replace();
            });
        },

        mostrarModal() {
            this.showModal = true;
        },

        cerrarModal() {
            this.showModal = false;
            this.errores.co_nombre = false;
            this.juzgados.co_id = "";
            this.juzgado.co_nombre = "";
        }
    },
};
</script>
  
<style scoped>
td {
    white-space: normal !important;
    word-wrap: break-word;
}

.circle {
    width: 30px;
    height: 30px;
    background-color: #7267EF;
    color: white;
    font-weight: 600;
    border-radius: 50%;
    text-align: center;
    line-height: 30px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal-content {
    background: #fff;
    width: 500px;
    height: auto !important;
    /* padding: 20px; */
    border-radius: 8px !important;
    box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px, rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
    position: relative !important;
}

.card-header {
    border-radius: 8px 8px 0px 0px !important;
}

.has-error {
    border: 1px solid #EA4D4D;
}

.error-message {
    padding-left: 15px;
    color: #EA4D4D;
    font-size: 12px;
}
</style>
  